* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Arial", sans-serif;
  background-color: #c39898;
}
#drum-machine {
  text-align: center;
  margin-top: 50px;
}
.drum-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 600px;
  margin: 0 auto;
  border: #000 2px solid;
  padding: 20px;
  border-radius: 10px;
  background-color: #e0e0e0;
}
#display {
  margin: 20px auto;
  padding: 10px;
  width: 300px;
  border: 2px solid #000;
  border-radius: 5px;
  background-color: #f0f0f0;
}
.drum-pad {
  display: inline-block;
  margin: 10px;
  padding: 20px;
  width: 70px;
  height: 70px;
  line-height: 80px;
  border: 2px solid #000;
  border-radius: 5px;
  background-color: #ccc;
  cursor: pointer;
  user-select: none;
  font-weight: bold;
}
.drum-pad.active {
  background: #c39898;
  transform: scale(0.95);
  box-shadow: 0 0 10px #c39898;
}
.drum-pad:hover {
  background-color: #bbbbbb47;
}

.drum-pad:focus {
  outline: none;
  box-shadow: 0 0 5px #000;
}
